/* =========== REUSABLES ========= */
.section {
  padding: 6rem 0 2rem;
}

.sectionTitle {
  font-size: var(--h1FondSize);
  color: var(--titleColor);
}

.sectionSubTitle {
  display: block;
  font-size: var(--smallerFondSize);
  margin-bottom: 4rem;
}
.sectionTitle,
.sectionSubTitle {
  text-align: center;
}
/* =========== LAYOUT ========= */
.container {
  max-width: 969px;
  margin-left: auto;
  margin-right: auto;
}

.grid {
  display: grid;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

