.header {
  width: 100%;
  position: fixed;
  height: 70px;
  top: 0;
  left: 0;
  z-index: 100;
  background-color: #23394d;
  padding-top: 10px;
}

.nav {
  height: calc(var(--headerHeight) + 1.5rem);
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
}
.navLogo,
.navToggle {
  color: white;
  font-weight: 500;
}

.navList {
  display: flex;
  grid-column-gap: 2rem;
  -webkit-column-gap: 2rem;
          column-gap: 2rem;
}
/* .navList:hover {
  border-radius: 1rem;
  background-color: #151320;
  padding: 10px 10px 10px 10px;
  height: 50px;
  font-weight: 90px;
} */

.navLink {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 0.875rem;
  color: white;
  font-weight: 500;
  transition: 0.3s;
}
.navIcon,
.navClose,
.navToggle {
  display: none;
}

/* Active - Link */
.activeLink,
.navLink:hover {
  color: #bb728f;
  border-radius: 1rem;
  background-color: #151320;
  padding: 10px 10px 10px 10px;
  height: 50px;
  font-weight: 120px;
}
.navLogo:hover {
  color: #bb728f;
  border-radius: 1rem;
  background-color: #151320;
  padding: 10px 10px 10px 10px;
  height: 50px;
  font-weight: 120px;
}

/* ============= BREAKPOINT =============== */

@media screen and (max-width: 768px) {
  .header {
    top: initial;
    bottom: 0;
  }
  .nav {
    height: 2.75rem;
  }

  .navMenu {
    position: fixed;
    bottom: -100%;
    left: 0%;
    width: 100%;
    background-color: #bb728f;
    padding: 2rem 1.5rem 4rem;
    box-shadow: 0 -1px 4px rgb(0, 0, 0, 0, 0.15);
    border-radius: 1.5rem 1.5rem 0 0;
    transition: 0.5s;
  }
  /* show menu */
  .showMenu {
    bottom: 0;
  }

  .navList {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
    gap: 2rem;
  }
  .navIcon {
    font-size: 1.2rem;
  }
  .navClose {
    position: absolute;
    right: 1.3rem;
    bottom: 5rem;
    font-size: 1.5rem;
    cursor: pointer;
    color: white;
  }

  .navClose:hover {
    color: #bb728f;
  }

  .navToggle {
    font-size: 1.1rem;
    cursor: pointer;
  }

  .navIcon,
  .navClose,
  .navToggle {
    display: block;
  }
}

@media screen and (max-width: 350px) {
  .navMenu {
    padding: 2rem 0.25rem 4rem;
  }
  .navList {
    grid-column-gap: 0;
    -webkit-column-gap: 0;
            column-gap: 0;
  }
}

